
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
h1, h2 {
  font-weight: normal; }

ul {
  list-style-type: none;
  padding: 0; }

li {
  display: inline-block;
  margin: 0 10px; }

a {
  color: #42b983; }

[class^="rj-icon-"],
[class*=" rj-icon-"] {
  display: inline-block;
  background-repeat: no-repeat; }

.rj-icon-vue-logo {
  background: url(data:image/png;base64,);
  width: 200px;
  height: 200px; }

.person--caocao {
  display: inline-block;
  width: 200px;
  height: 200px;
  background: url(../../static/img/caocao.09c66af.jpg); }

.person--zhangfei {
  display: inline-block;
  width: 100px;
  height: 200px;
  background: url(../../static/img/zhangfei.86bd8e7.jpg); }

.person--zhaoyun {
  display: inline-block;
  width: 100px;
  height: 200px;
  background: url(data:image/jpeg;base64,); }

.person--machao {
  display: inline-block;
  width: 100px;
  height: 200px;
  background: url(data:image/jpeg;base64,); }

.person--guanyu {
  display: inline-block;
  width: 200px;
  height: 100px;
  background: url(../../static/img/guanyu.37b3127.jpg); }

.person--huangzhong {
  display: inline-block;
  width: 100px;
  height: 200px;
  background: url(data:image/jpeg;base64,); }

.person--xiaobing {
  display: inline-block;
  width: 100px;
  height: 100px;
  background: url(data:image/jpeg;base64,); }

.banner {
  background: url(data:image/png;base64,);
  display: inline-block;
  width: 32px;
  height: 32px; }

.walkway {
  position: relative;
  background-color: #E6A23C;
  width: 400px;
  height: 500px; }

.walkway__exit {
    position: absolute;
    bottom: 0;
    width: 50%;
    left: 25%;
    text-align: center;
    height: 100px;
    line-height: 100px; }

.walkway__exit span {
      white-space: pre-wrap;
      font-size: 38px; }

.person {
  border: 1px solid #DCDFE6;
  z-index: 3;
  margin: 0;
  position: absolute; }

.person::hover {
    border-color: #E4E7ED; }

.person.active {
    border: 2px solid #E4E7ED; }

.person .banner {
    position: absolute; }
